import * as axios from 'axios'
const url = 'https://atie.leoyuholo.com/predict_foodwaste'
let turnOnCam = function () {
    return navigator.mediaDevices.getUserMedia({
          audio: false,
          video: { facingMode: "environment" }
        })
}

let isMobile = function () {
    // let isMobileDevice = navigator.userAgent.match(/iPad|iPhone|iPod/i) != null 
    // || screen.width <= 480;
    let check = (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent))
    return check
}

let transform = function (data) {
    data.score = parseInt(data.score)
    return data
}

let drawImage = function (canvas, path) {
    var img = new window.Image();
    img.addEventListener("load", function () {
        canvas.getContext("2d").drawImage(img, 0, 0);
        canvas.toBlob( function(blob) {
          axios.post(url, blob, {
          headers: {
            'Access-Control-Allow-Origin': 'true',
            'Content-Type': blob.type
          }
        }).then((d) => {
          console.log(d)
        })
      }, "image/jpeg", 0.95)
    });
    img.setAttribute("src", path);
}

let compressImage = function (base64URL, callback){
    let img = new Image()
    let canvas = document.createElement('canvas')
    let context = canvas.getContext('2d')
    img.src = base64URL
    img.onload = () => {
        let originWidth = img.width
        let originHeight = img.height
        let maxWidth = 400,
        maxHeight = 400
        let targetWidth = originWidth,
        targetHeight = originHeight
        // 图片尺寸超过400x400的限制
        if (originWidth > maxWidth || originHeight > maxHeight) {
            if (originWidth/originHeight > maxWidth/maxHeight) {
                //更宽，按照宽度限定尺寸
                targetWidth = maxWidth
                targetHeight = Math.round(maxWidth * (originHeight / originWidth))
            } else{
                targetHeight = maxHeight
                targetWidth = Math.round(maxHeight * (originWidth / originHeight))
            }
        }
        canvas.width = targetWidth
        canvas.height = targetHeight
        context.clearRect(0, 0, targetWidth, targetHeight)
        context.drawImage(img, 0, 0, targetWidth, targetHeight)
        let base64Data = canvas.toDataURL("image/png", 0.92)
        let blob = dataURItoBlob(base64Data) //upload
        let params = new FormData()
        params.append('visit_file', blob, 'cavas.png')
        callback();
    }
}
  
let dataURItoBlob = function (base64Data) {
    let bytes = window.atob(base64Data.split(',')[1]) //去掉url的头，并转换为byte
    // 处理异常,将ascii码小于0的转换为大于0
    let ab = new ArrayBuffer(bytes.length)
    let ia = new Uint8Array(ab)
    for (let i = 0; i < bytes.length; i++) {
        ia[i] = bytes.charCodeAt(i)
    }
    return new Blob([ab], {
        type: 'image/png'
    })
}

export {
    compressImage,
    turnOnCam,
    drawImage,
    transform,
    isMobile
}