/* eslint-disable */
import * as d3 from 'd3' 
import * as d3Shape from 'd3-shape'
const lookup = {
    'others': '❓',
    'other': '❓',
    'meat': '🥩',
    'vegetables': '🥦',
    'rice': '🍚'
}
const stripstr = `<defs>
        <pattern id="pattern_blue" patternUnits="userSpaceOnUse" width="6.5" height="6.5" patternTransform="rotate(135)">
			<line x1="0" y="0" x2="0" y2="6.5" stroke="#0876E4" stroke-width="5" />
		</pattern>
	</defs>`

const COLOR = {
    major: '#0876E4',
    bg: '#f3f3f3'
}
let getStrip = function(id, color, rotate) {
    return `<defs>
        <pattern id="${id}" patternUnits="userSpaceOnUse" width="9.5" height="9.5" patternTransform="rotate(${rotate})">
        <line x1="0" y="0" x2="0" y2="9.5" stroke="${color}" stroke-width="13" />
        </pattern>
    </defs>`
}

let plotFeedback = function (data, divid) {
    const margin = {
        top: 0.05,
        bottom: 0.2,
        left: 0.01,
        right: 0.02
    }
    let div = d3.select(`#${divid}`)
    let bbox = div.node().getBoundingClientRect()
    const height = bbox.height
    const width = bbox.width
    let canvas = div.html('').append('svg')
        .attr('width', width)
        .attr('height', height)
        .append('g')
        .attr('transform', `translate(${margin.left * width}, ${margin.top * height})`)
    
    data.sort((a, b) => a.amount > b.amount)
    let amountMax = d3.max(data, d => d.amount) * 1.5
    let xScale = d3.scaleLinear()
        .domain([0, amountMax])
        .range([0, width * (1 - margin.left - margin.right)])

    let yScale = d3.scaleBand()
        .domain(data.map(v => v.type))
        .range([ - height * margin.top , height * (1 - margin.top - margin.bottom)])
        .padding(1);

    let lolipops = canvas.append('g').classed('lolipop-canvas', true)
        .selectAll('.lolipop')
        .data(data)
        .enter()
        .append('g')
        .classed('lolipop', true)
    
    lolipops.append('path')
        .attr('d', d => `M ${xScale(0)} ${yScale(d.type)} L ${xScale(d.amount)} ${yScale(d.type)} Z`)

    let labels = lolipops.append('g')
        .attr('transform', d => `translate(${xScale(d.amount)}, ${yScale(d.type)})`)
    labels.append('text')
        .datum(d => d)
        .attr('y', 4)
        .text(d => lookup[d.type])
        .classed('shake', true)
        .classed('svg-icon', true)
    labels.append('text')
        .attr('x', 20)
        .attr('y', 4)
        .text(d => `${d.type}: ${parseInt(d.amount)}`)
   
    canvas.append('g').classed('axis', true)
        .attr('transform', `translate(${0}, ${yScale.range()[1]})`)
        .call(d3.axisBottom(xScale).ticks(6))
}

let plotPie = function(data, gen, g, colors) {
    g.append('g')
        .classed('pie-area', true)
        .selectAll('path')
        .data(data)
        .enter()
        .append('path')
        .attr('d', d => gen({
            startAngle: d.startAngle,
            endAngle: d.endAngle
        }))
        .style('fill', (d,i) => colors[i])
}

let drawPieLegend = function (pieg, piesize, pies, colorList) {
    const ratio = 0.5
    const height = 2 * piesize * ratio / pies.length
    const midIndex = (pies.length + 1) / 2

    let slots = pieg.append('g').classed('pie-legend', true)
        .selectAll('g')
        .data(pies)
        .enter()
        .append('g')
        .classed('slot', true)
        .attr('transform', (d, i) => `translate(${-piesize*ratio*1.1}, ${(i+1-midIndex)*height})`)
        .append('text')
        .attr('x', 0)
        .style('text-anchor', 'left').attr('x', 0)
    
    slots.append('tspan')
        .text('●')
        .style('font-size', '1.3rem')
        .style('fill', (d,i) => colorList[i])
        .style('dominant-baseline', 'middle')
        
    slots.append('tspan')
    .text(d => ' ' + d.data.type)
    .style('fill', (d,i) => COLOR.major)
    .style('dominant-baseline', 'middle')

}

let drawPercentageLabel = function (statg, data) {
    let text = Number((data.total - data.foodwaste)*100/data.total).toFixed(1)
    let update = function(g) {
        g.append('tspan')
            .text(`${text}`)
            .attr('x', 0)
            .style('font-size', '2rem')
            .style('font-weight', 800)
            .style('dominant-baseline', 'middle')
        g.append('tspan')
            .text('%')
            .style('font-size', '.9rem')
            .style('dominant-baseline', 'middle')
        g.append('tspan')
            .text('Eaten')
            .attr('x', 0)
            .attr('dy', '1.5rem')
            .style('font-size', '.9rem')
    }
    let bgInform = statg.append('g')
    .append('text')
    .style('text-anchor', 'middle').attr('x', 0)
    .style('fill', COLOR.major)
    .style('stroke', COLOR.bg)
    .style('stroke-width', 8)
    let inform = statg.append('g')
        .append('text')
        .style('text-anchor', 'middle').attr('x', 0)
        .style('fill', COLOR.major)
    update(bgInform)
    update(inform)
}

let plotWastePie = function (data, divid) {
    const margin = {
        top: 0.05,
        bottom: 0.05,
        left: 0.02,
        right: 0.02
    }
    const offset = Math.PI/6
    let div = d3.select(`#${divid}`)
    let bbox = div.node().getBoundingClientRect()
    const height = bbox.height*(1-margin.bottom-margin.top), width = bbox.width*(1-margin.left-margin.right)
    let piesize = Math.min(height, width) * 0.85 / 2
    let halfsize = Math.max(2 * piesize, width / 2 * 0.8)
    let svg = div.select('svg').html('').html(stripstr).append('g')
        .attr('transform', `translate(${margin.left * width}, ${margin.top * height})`)
    let pieg= svg.append('g').classed('svg-piechart', true).attr('transform', `translate(${halfsize-piesize}, ${height/2})`)
    let statg = svg.append('g').classed('svg-state', true).attr('transform', `translate(${width - halfsize + piesize}, ${height/2})`)
    //buttong = svg.append('g').classed('svg-button', true)
    const colorList = [ '#96D7F2','#32BFF2', '#0572CD','#0063AA']
    const arcGenerator = d3Shape.arc().innerRadius(piesize*0.8).outerRadius(piesize).cornerRadius(2).padAngle(Math.PI/135)
    const slideGenerator = d3Shape.arc().innerRadius(piesize*0.1).outerRadius(piesize*1.25).cornerRadius(2).padAngle(Math.PI/135)
    const pieDataWrapper = d3Shape.pie().value(d => d.portion).sort((a,b) => (a.portion > b.portion))
    let pies = pieDataWrapper(data.distribution)

    let stats = pieDataWrapper.startAngle(offset).endAngle(2*Math.PI+offset)([{portion: data.foodwaste}, {portion: data.total - data.foodwaste}])
    plotPie(pies, arcGenerator, pieg, colorList)
    drawPieLegend(pieg, piesize, pies, colorList)


    plotPie([stats[1]], arcGenerator, statg, [COLOR.major])
    plotPie([stats[0]], slideGenerator, statg, ['url(#pattern_blue)'])
    drawPercentageLabel(statg, data)
}

let plotWasteLine = function (data, divid) {
    const margin = {
        top: 0.15,
        bottom: 0.12,
        left: 0.1,
        right: 0.05
    }
    const offset = Math.PI/6
    let div = d3.select(`#${divid}`)
    let bbox = div.node().getBoundingClientRect()
    const realHeight = bbox.height, realWidth = bbox.width
    const height = realHeight*(1-margin.bottom-margin.top), width = realWidth *(1-margin.left-margin.right)
    let svg = div.select('svg').attr('height', realHeight).attr('width', realWidth).html('').append('g').attr('transform', `translate(${realWidth * margin.left}, ${realHeight * margin.top})`)
    const parser = d3.timeParse(data.timeFormat)
    let yMin = Number.POSITIVE_INFINITY, yMax = Number.NEGATIVE_INFINITY
    data.summary.forEach(v => {
        v.time = parser(v.time)
        yMin = Math.min(yMin, v.value)
        yMax = Math.max(yMax, v.value)
    })
    const startTime = data.summary[0].time
    const endTime = data.summary[data.summary.length - 1].time
    const timeDelta =  endTime - startTime
    let xScale = d3.scaleTime()
        .range([0, width])
        .domain([startTime, new Date(endTime.getTime() + timeDelta * 0.1)])
    let yScale = d3.scaleLinear()
        .range([height, 0])
        .domain([0, yMax + (yMax- yMin)/5])
    svg.append('g')
        .attr('transform', `rotate(0)`)
        .classed('axis', true)
        .classed('y-axis', true)
        .call(d3.axisLeft(yScale).ticks(6).tickSize(-width))
        .call((g) => {
            g.select('.domain')
                .remove()
            g.selectAll('.tick line')
                .style("stroke-opacity", 0.5)
                .style("stroke-dasharray", "2,2")
            g.select('.tick:first-of-type text')
                .remove()       
            g.select('.tick:last-of-type text')
            .text('(Ton)')   
            .attr('dy', '-3')   
        })
    svg.append('g')
    .attr('transform', `translate(${0}, ${height})`)
    .classed('axis', true).classed('x-axis', true)
    .call(d3.axisBottom(xScale).ticks(4).tickSize(-height))
    .call((g) => {
        g.select('.domain').remove()
        g.selectAll('.tick line')
        .style("stroke-opacity", 0.5)
        .style("stroke-dasharray", "2,2")
    })
   let lineGenerator = d3.line()
        .defined(d => !isNaN(d.value))
        .curve(d3.curveCardinal)
        .x(d => xScale(d.time))
        .y(d => yScale(d.value))
    let canvas = svg.append('g').classed('linechart', true)
      canvas.append('path')
      .datum(data.summary)
      .attr('d', d => lineGenerator(d))
    canvas.append('g').classed('scatter', true)
        .selectAll('circle')
        .data(data.summary)
        .enter()
        .append('circle')
        .attr('cx', d => xScale(d.time))
        .attr('cy', d => yScale(d.value))
        .attr('r', 2)
    canvas.select('circle:last-of-type')
        .attr('r', 5)
    let cur = data.summary[data.summary.length - 1]
    canvas.append('g')
        .append('text').classed('label', true)
        .attr('x', xScale(cur.time) + 10)
        .attr('y', yScale(cur.value))
        .text(cur.value)
}

export {
    plotFeedback,
    plotWastePie,
    plotWasteLine
}
