<template>
  <header class="navbar">
    <a-row type="flex" align="middle" justify="space-between">
      <a-col :span="2">
        <i class="center fas fa-utensils"></i>
      </a-col>
      <a-col :span="17">
        <router-link to="/"><span class="title">Food Minus</span></router-link>
      </a-col>
      <a-col v-if="needUpload" :span="3">
        <router-link to="/feedback">
          <a-button type="primary">
            <i class="fas fa-camera-retro"></i>
          </a-button>
        </router-link>
      </a-col>

      <a-col v-if="!needUpload" :span="3">
        <router-link to="/profile">
          <a-button type="primary">
            <i class="fas fa-user-circle"></i>
          </a-button>
        </router-link>
      </a-col>
    </a-row>
  </header>
</template>

<script>
import Button from "ant-design-vue/lib/button";
import Row from "ant-design-vue/lib/row";
import Col from "ant-design-vue/lib/col";
import { mapGetters } from 'vuex';
export default {
  name: "Navbar",
  components: {
    [Button.name]: Button,
    [Row.name]: Row,
    [Col.name]: Col,
  },
   computed: {
    ...mapGetters(['action']),
    needUpload() {
      let hour = (new Date()).getHours() + 1
      if(hour < 11) return !this.action.morning
      if(hour < 17) return !this.action.noon
      return !this.action.afternoon
    }
  },
};
</script>
