import { createStore } from 'vuex'

export default createStore({
  state: {
    dev: true,
    action: {
      morning: false,
      noon: false,
      afternoon: false
    }
  },
  getters: {
    dev(state) {
      return  state.dev
    },
    action(state) {
      return state.action
    }
  },
  mutations: {
    update(state, payload) {
      if(!('subfield' in payload)) {
        state[payload.field] = payload.value
      }
      else {
        state[payload.field][payload.subfield] = payload.value
      }
      console.log(state)
    }
  },
  actions: {
    updateAction(context) {
      let hour = (new Date()).getHours() + 1
      let payload = {
        subfield: hour < 11 ? 'morning' : (hour < 17 ? 'noon' : 'afternoon'),
        field: 'action',
        value: true
      }
      context.commit('update', payload)
    }
  },
  modules: {
  }
})
