<template>
  <div id="SchoolView">
    <div class="card board barchart">
      <div class="chart_wrapper" id="today_pie">
        <div class="pad chart_title">Today's HKUST</div>
        <svg></svg>
      </div>
    </div>
    <div class="card board linechart">
      <Carousel :items-to-show="1" :wrapAround="true">
        <Slide v-for="gran in grans" :key="gran">
          <div class="carousel__item chart_wrapper" :id="`line_${gran}`">
            <div class="chart_title">
              {{ `${lookup(gran)}` }}
            </div>
            <svg></svg>
          </div>
        </Slide>

        <template #addons>
          <Navigation />
          <!-- <Pagination /> -->
        </template>
      </Carousel>
    </div>

    <div class="card pad board leaderboard">
        <div class="chart_title">Leaderboard</div>
        <div class="table">
        <a-row class="field" justify="space-between" align="middle">
          <a-col span="1">#</a-col>
          <a-col span="11">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Name</a-col>
          <a-col v-if="abbr" span="2">Type</a-col>
          <a-col span="2">Dept</a-col>
          <a-col span="4" class="score">Score</a-col>
        </a-row>
        </div>
        <div class="table">
        <a-row
          v-for="(item, index) in leaders"
          :key="item"
          justify="space-between"
          align="middle"
        >
          <a-col span="1">
            {{ index + 1 }}
          </a-col>
          <a-col span="11">
          <a-avatar v-if="abbr" size="small" :icon="item.name[0]" ></a-avatar>
          <span v-if="abbr"> &nbsp; </span>
          <span> {{ getName(item.name) }} </span>
          </a-col>
          <a-col v-if="abbr" span="2">
            {{ item.type }}
          </a-col>
          <a-col span="2">
            {{ item.school }}
          </a-col>
          <a-col span="4" class="score"> {{ item.score }}pt </a-col>
        </a-row>
    </div>
    </div>
  </div>
</template>
<script>
import * as d3 from "d3";
import { Carousel, Slide, Navigation } from "vue3-carousel";
import Row from "ant-design-vue/lib/row";
import Col from "ant-design-vue/lib/col";
import Avatar from "ant-design-vue/lib/avatar";
import { plotWastePie, plotWasteLine } from "@/assets/js/plot";
export default {
  name: "Overview",
  components: {
    Carousel,
    Slide,
    Navigation,
    [Row.name]: Row,
    [Col.name]: Col,
    [Avatar.name]: Avatar,
    // Pagination
  },
  data() {
    return {
      grans: ["day", "week", "month", "year"],
      leaders: [],
      thresh: 300
    };
  },
  computed: {
    abbr() {
      return window.innerWidth > 320 ? true : false 
    }
  },
  mounted() {
    let self = this;
    d3.json("./dataset/mock-school.json").then((data) => {
      console.log("school data", data);
      plotWastePie(data.today, "today_pie");
      self.grans.forEach((gran) => {
        plotWasteLine(data.trend[gran], `line_${gran}`)
      })
    });
    d3.json("./dataset/mock-leaderboard.json").then((data) => {
      self.leaders = data;
    });
  },
  methods: {
    lookup(text) {
      if (text == "day") return "Today";
      if (text == "week") return "1 Week";
      if (text == "month") return "1 Month";
      if (text == "year") return "1 Year";
      return "";
    },
    getName(text) {
      if (window.innerWidth > 400) return text
      if (text.length < 12) return text
      let comps = text.split(' ')
      return comps[0][0] + '. ' + comps[comps.length-1]
    }
  },
};
</script>
